@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
/* colors */
.jumbotron {
  position: relative;
  display: flex;
  width: 100%;
  height: 100vh;
  padding-bottom: 10px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }
  .jumbotron__overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    padding: 100px 50px;
    color: white; }
  .jumbotron__post {
    width: 60%; }
  .jumbotron__title {
    line-height: 65px; }
    .jumbotron__title {
      font-size: 1.424rem; }
      @media screen and (min-width: 20rem) {
        .jumbotron__title {
          font-size: calc(1.44551vw + 1.1349rem); } }
      @media screen and (min-width: 85.375rem) {
        .jumbotron__title {
          font-size: 2.369rem; } }
  .jumbotron__text {
    opacity: 0.79;
    letter-spacing: -0.3px;
    margin-top: 10px;
    color: #fff; }
  .jumbotron__button {
    padding: 10px 16px;
    border-radius: 25px;
    text-transform: uppercase;
    font-size: 0.7rem;
    cursor: pointer;
    color: #444;
    background-color: #1292b4; }
    .jumbotron__button:focus {
      outline: none; }
    .jumbotron__button:hover {
      color: #fff;
      background-color: #444; }
      .jumbotron__button:hover:focus {
        outline: none; }

@media (max-width: 599px) {
  .jumbotron__overlay {
    padding: 0; }
  .jumbotron__post {
    width: 100%; }
  .jumbotron__title {
    margin-top: 0;
    width: 100%;
    font-size: 2.2rem;
    letter-spacing: unset;
    line-height: unset; }
  .jumbotron__text {
    line-height: unset; } }

